<template>
    <div class='random-recipe-box'>
        <!-- <van-image  width="200px" :src="require('/static/img/green.png')"></van-image> -->
        <div v-show="albumArr.length" class='random-title'>大家都在做</div>
        <div v-show="albumArr.length" class="swiper-container" id="randomLookLixt">
            <swiper :options="swiperOption" ref="cookingSwiper" style='width:100%;height:100%'>
              <swiper-slide v-for="(item,index) in albumArr" :key="index" class="swiper-slide page">
                <div class="album_img" @click='skipRecipeDetail(item)'>
                    <van-image
                            width="100%"
                            height="100%"
                            fit="cover"
                            :loading-icon='defaultImg'
                            :error-icon='defaultImg'
                            :src="`${item.imgSmall?item.imgSmall:(item.imgMedium?item.imgMedium:(item.imgLarge?item.imgLarge:defaultImg))}`"/>
                </div>
                <div class='down-box' @click='skipRecipeDetail(item)'>
                     <div class='left-title-box'> 
                         <div class='name'>
                             {{item.name}}
                         </div>
                         <div class='sub-title'>{{item.collectCount?item.collectCount:0}}人收藏</div>
                     </div>
                     <div class='right'>
                         <van-image
                            width="18px"
                            fit="contain"
                            :src="require('@/assets/images/label.png')"/>
                     </div>
                </div>
              </swiper-slide>
            </swiper>
        </div>
    </div>
</template>
<script>
import { swiper, swiperSlide } from "vue-awesome-swiper";
import "swiper/css/swiper.css";  
import { getRandomRecipe } from '@/api/lineViewsFromRokiApp';
export default {
    components:{
        swiper,
        swiperSlide,
    },
    data() {
        return {
            // host: config.host,
            albumArr:[],
            swiperOption:{
                direction: 'horizontal',
                watchSlidesProgress: true,
                watchSlidesVisibility: true,
                slidesPerView: 2.4,
                spaceBetween: 12,
                observer: true,
                observeParents: false,
            },//swiper选项
            defaultImg:require('@/assets/images/roki_placehoder.png')
        }
    },
    props:{
       skipParams:{
           type:Object,
           default:()=>{}
       }
    },
    computed: {
        swiper() {
            return this.$refs.cookingSwiper.swiper;
        }
    },
    created(){
         
    },
    mounted(){
        this.getList();
         
    },
    methods:{
        getList(){
            const params={
                type:-1,
                pageSize:5,
                pageNo:0
            }
 
            getRandomRecipe(params).then(res=>{
                console.log('res-------',res);
             
                    const { cookbooks }=res;
                    this.albumArr=cookbooks;
                    // this.swiperOption={
                    //     // direction: 'horizontal',
                    //     // slidesPerView: 2.4,
                    //     // spaceBetween: 11,
                    //     // freeMode: true,
                    //     // observer: true,
                    //     // observeParents: false,      
                    // }
            }).catch(err=>{
         
                console.log('err',err);
            })
     
             
        },
      //跳转到菜谱详情页h5
      skipRecipeDetail(curr){
          console.log('curr',curr);
          window.location.href=`https://h5.myroki.com/dist/index.html#/recipeDetail?cookbookId=${curr.id}`
          //判断当前环境-小程序
            // console.log('config',config)
            // const currentEnv=config.browser.versions.isWeixin;
            // if(currentEnv){
            //     wx.miniProgram.getEnv((res)=>{
            //         if (res.miniprogram) {
            //             //console.log("在小程序里");
            //             wx.miniProgram.redirectTo({url:`/pages/cookbook/cookbook?cookbookId=${curr.id}`})
            //         } else {
            //             // console.log("不在小程序里");
            //             const {entranceCode}=this.skipParams
            //             // console.log('entranceCode',entranceCode)
            //             this.$router.replace({path:'/emptyRedirect',query:{router:`/recipeDetail?cookbookId=${curr.id}&&entranceCode=${entranceCode}`}})
            //         }
            //     })
            // }else{
            //     const {entranceCode}=this.skipParams
            //     // console.log('entranceCode',entranceCode)
            //     this.$router.replace({path:'/emptyRedirect',query:{router:`/recipeDetail?cookbookId=${curr.id}&&entranceCode=${entranceCode}`}})
                
            // }
      }
    }
}
</script>
<style lang='scss' scoped>
.random-recipe-box .down-box .name{
    font-size: 14px;
    font-weight: 500;
    color: #333333;
    margin-bottom:3px;
}
.random-recipe-box .down-box .sub-title-box{
    font-size: 12px;
    font-weight: 400;
    color: rgba(153, 153, 153, 0.85);
}
.down-box{
    padding: 8px 7px 7px 10px;
    display: flex;
    justify-content: space-between;
    .left-title-box{
        display:flex;
        flex-direction:column;
        justify-content: space-between;
        text-align: left;
        width: 80%;
        .name{
            font-size: 14px;
            font-family: Roboto-Regular, Roboto;
            font-weight: 400;
            color: #222222;
            // line-height: 16px;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
        }
        .sub-title{
                font-size: 12px;
                font-family: Roboto-Regular, Roboto;
                font-weight: 400;
                color: rgba(34, 34, 34, 0.5);
        }
    }
}
.random-title{
    text-align: left;
    margin: 12px 0 11px 18px;
    font-size: 19px;
    font-family: OPPOSans-Bold, OPPOSans;
    font-weight: bold;
    color: #333333;
} 
.swiper-container{
    padding-left:10px;
}
.swiper-slide{
    &.page{
        box-shadow: 0 3px 6px 0 rgba(30,30,30,0.05);
        margin-bottom:0.2rem;
        border-radius:0.05rem; 
        .album_img{
            height :117px;
            overflow: hidden;
            font-size :0;
            overflow:hidden;
            border-radius : 15px 15px 0 0;
            background-size:cover;
        }
        .thumbUp{
            font-size:0;
            padding-left:0.12rem;
            padding-bottom:0.14rem;
            img{
                width:0.2rem;
                height:0.2rem;
            }
            span{
                display:inline-block;
                line-height:0.12rem;
                font-size:0.14rem;
                vertical-align:top;
                margin-top:0.05rem;
                margin-left:0.12rem;
            }       
                 
                   
        }
                         
    }
}
                
</style>
 



