<template>
    <div class='muti-box'>
        <!--分享人-->
        <div class='people-box'>
            <div>
                <van-image
                width="46px"
                height="46px"
                :round='true'
                fit="contain"
                :src="params.avatar"
                />  
            </div>
            <div class='share-people' v-if='params.name'>来自{{params.name}}的分享</div>
        </div>
        <muti-detail :mutiId='params.id'/>
        <!--大家都在做-->
        <random-recipe/>
        <!--指引-->
        <div class="rightan">
            <van-image
                width="24px"            
                :round='true'
                fit="contain"
                :src="require('@/assets/images/arrow.png')"
                />
        </div>
        <div class='guide-title' @click='open'>打开ROKI APP,查看更多美味</div>
        <div class='keep-btn'>
            <van-button color="#F2B33E" size='large' :round='true' @click='keepAlumns' style='font-size:17px;font-weight:400'>保存到ROKI</van-button>
        </div>
        <!--多段分享图片-->
        <!-- <poster v-if='showPoster' :mutiArr='mutiArr' @close='showPoster=false'/> -->
        <!--提示到浏览器打开蒙层-->
        <!-- <div class="mask-block" v-show='isDisplay' @click='showPoster=false'>
            <div class="mask-content">
                <div class="mask-text">
                    <p>点击右上角...按钮</p>
                    <p>选择在  浏览器中打开  即可下载</p>
                </div>
                <img :src="require('@/assets/images/guide_arrow.png')" alt="">
            </div>
        </div> -->
    </div>
</template>
<script>
import mutiDetail from './mutiDetail';
import randomRecipe from './randomRecipe';//随机菜谱
import poster from './canvasLayer';
import CallApp from 'callapp-lib';
import { browser } from '@/utils';
import { keepMutiInfo } from '@/api/lineViewsFromRokiApp';
export default {
    name:'lineViewsFromRokiApp',//多段烹饪
    components:{
        randomRecipe,
        poster,
        mutiDetail
    },
    data() {
        return {
            params:{
                id:'',
                avatar:'',
                name:''
            },
            showPoster:false,
            isDisplay:true,
        }
    },
    created(){
        //console.log(this.$route.query);
        const {id,avatar,name}=this.$route.query;
        this.params={
            id,
            avatar,
            name
        }
    },
    methods:{  
        //保存到roki
        keepAlumns(){
            //this.showPoster=true;
            const userInfo=JSON.parse(localStorage.getItem('userInfoke'));
            //console.log('userInfo----userInfo---',userInfo);
            if(userInfo&&userInfo.id){
                //已经登录过--直接保存
                keepMutiInfo(this.params.id,{userId:userInfo.id}).then(res=>{
                    //console.log('res---保存多段---',res);
                    this.$toast('保存多段信息成功');
                })  
            }else{
                this.$router.push(`/login?mutiId=${this.params.id}&lastLabel=keepMuti`);
            }
        },
        open(){
            const ua = navigator.userAgent || '';
            if(ua.match(/(iPhone|iPod|iPad);?/i)){
                window.location.href = 'https://oss.myroki.com/webSkipApp/ulinkFailSkipApp.html';//universal link方式，
            }else if(ua.match(/android/i)) {
	            //window.location.href =  'https://a.app.qq.com/o/simple.jsp?pkgname=com.robam.roki';
                if(ua.indexOf('MicroMessenger') > -1){
                    window.open('https://a.app.qq.com/o/simple.jsp?pkgname=com.robam.roki')  
                }else{
                    //直接跳转app
                    window.location.href = "scheme://roki/welcome";
                    window.setTimeout(function(){
                        window.location.href = "https://a.app.qq.com/o/simple.jsp?pkgname=com.robam.roki"; /***打开app的协议，有安卓同事提供***/
                    },2000);
                }
		        //window.location.href ="https://webcdn.m.qq.com/webapp/homepage/index.html#/appDetail?apkName=com.robam.roki&info=C2E96B3246F66EAFB2698CF967E1DD71"
                // window.setTimeout(function(){
		        //    window.location.href = "http://a.app.qq.com/o/simple.jsp?pkgname=com.huaqimy.huaqimy"; /***打开app的协议，有安卓同事提供***/
		        // },2000);
			}
      
        },
    }
}
</script>
<style lang='scss' scoped>
.muti-box{
    // padding:15px 0 20px 15px;
    .people-box{
       display:flex;
       align-items:center;
       padding: 12px 18px;
        .share-people{
            margin-left:17px;
            font-size: 15px;
            font-family:"Microsoft YaHei";
            font-weight: 500;
            color: #333333;
        }
    }
 
    .random-recommend-box{
         .single-swiper{
                .single{
                    display:flex;
                    align-items: center;
                    justify-content: center;
                    color: #fff; 
                }
            }
    }
}
/*向下箭头*/
.rightan{
    margin: 18px 0 8px 0;
    animation: rightan 1s infinite;
    animation-fill-mode: both;
     
}
@keyframes rightan{				
  25% {transform: translateY(-3px);}
     50%{transform: translateY(0);}
      75% {transform: translateY(3px);}
      100% {transform: translateY(0);}
}
.guide-title{
    color:#93c9f1;
    text-align:center;
    font-size: 13px;
    font-family: OPPOSans-Regular, OPPOSans;
    font-weight: 400;
    color: #5FB2FF;
}	
.keep-btn{
 padding:15px;
 font-size: 17px;
font-family: Roboto-Regular, Roboto;
font-weight: 400;
}	
/*提示蒙层*/
.mask-none{
    // display: none;
}
.mask-block{
    position: fixed;
    z-index: 100;
    top:0;
    right: 0;
    bottom: 0;
    left: 0;
    display: block;
    background: rgba(0, 0, 0, .5);
}
.mask-content{
    position: relative;
}
.mask-text{
    font-size: 1.2rem;
    color: #fff;
    position: absolute;
    top: 2.6rem;
    right: 1.3rem;
}
.mask-content img{
    height: 3.8rem;
    position: absolute;
    top: 0.5rem;
    right: 0.3rem;
}		 
</style>



