import { regularReq } from '@/utils/request';
// const host = 'http://develop.api.myroki.com'
export function getMutiDetail(id){
    return regularReq({
        url: `/rest/cks/api/multi/${id}`,
        method: 'get'
    })
}
export function getRandomRecipe(data){
    return regularReq({
        url: '/rest/cks/api/cookbook/by-tag-other/get/cooks',
        method: 'post',
        data
    })
}

export function keepMutiInfo(id,data){
    return regularReq({
        url: `/rest/cks/api/multi/share/${id}`,
        method: 'post',
        data
    })
}


