<template>
    <div>
        <van-popup v-model="show" style='width:92%;background-color:transparent;font-variant: normal' :round='true' @close="$emit('close')">
            <div class='mut-poster-box' ref='posterCut'>
               <div class='muti-main-title'>多段烹饪</div> 
               <div class='share-info'>
                   <div class='avater-box'>
                       <van-image width="3rem" height="3rem" fit="contain" :round='true' :src="avatar"/>
                       <span class='name'>zhang 三分</span> 
                   </div>
                   <div class='date'>2021/12/06</div>
               </div>
               <muti-detail v-bind='$attrs'/>
               <div class='bottom-roki'>
                  <div class='sub-roki'>
                      <!--<van-image width="4rem" height="4rem" fit="contain" :src="avatar"/>-->
                      <vue-qr :text="qrcodeUrl" :margin="0" colorDark="#000" colorLight="#fff" :logoSrc="require('@/assets/images/shine.png')" :logoScale="0.2" :size="56"></vue-qr>
                      <span class='name' style='font-size:10px;'>扫二维码查看更多美食</span> 
                  </div>
                  <div class='right-logo'>
                      <van-image width="2.5rem" height="2.5rem" fit="contain" :src="avatar"/> 
                  </div>
               </div>
            </div>
            <div class='poster-div' style='overflow:hidden;'>
                <van-loading type="spinner" color="#1989fa" v-if='loading'/>
                <!-- <a :href="transformPoster" download='多段烹饪.png'>  -->
                <img v-if='!loading' :src="transformPoster" alt="" class='slide-in-elliptic-top-fwd' style='width:90%;border-radius:15px;'/>
                <!-- </a> -->
            </div>
        </van-popup>
    </div>
</template>
<script>
import mutiDetail from './mutiDetail';
import { savePicture,getBase64 } from '@/utils';
 
import vueQr from 'vue-qr';//生成二维码
import html2canvas from "html2canvas";
export default {
    components:{
        mutiDetail,
        vueQr,
       
    },
    data() {
        return {
            show:true,
            avatar:'',
            transformPoster:'',
            loading:false,
            qrcodeUrl:'http://172.16.21.148:9090/#/scanCodeSkip',
        }
    },
    created(){
        const { avatar }=this.$route.query;
        this.avatar=avatar;
    },
    mounted(){
        //将图片转成base64编码渲染
        this.transformFun();
        
    },
    methods:{
        async transformFun(){
            this.loading=true;
            let avater = await getBase64(this.avatar),//头像
             machineName=await getBase64(this.avatar),//机器型号
             codeImg=await getBase64(this.avatar),
             rokiLogo=await getBase64(this.avatar);
             this.avatar=avater;
             //const scale = 2;
            //  var canvas = document.createElement("canvas"); 
            //  canvas.width = this.$refs.posterCut.offsetWidth * scale;
            //  canvas.height =this.$refs.posterCut.offsetHeight* scale;
            //  canvas.style.width = this.$refs.posterCut.offsetWidth* scale+ "px";
            //  canvas.style.height = this.$refs.posterCut.offsetHeight* scale+ "px";
            //  canvas.getContext("2d").scale(scale, scale); 
                //设置放大倍数
                // const scale = window.devicePixelRatio;
                const scale =2;
                // console.log(this.$refs.posterCut.offsetWidth);
                const width=this.$refs.posterCut.outerWidth ;
                const height=this.$refs.posterCut.outerHeight ;
                // html2canvas配置项
                const ops = {
                    scale,
                    // canvas,
                    width,
                    height,
                    useCORS: true,
                    allowTaint: false,
                    dpi: window.devicePixelRatio*2,
                };
                setTimeout(()=>{
                    html2canvas(this.$refs.posterCut,ops).then(canvas => {
                        let dataURL = canvas.toDataURL("image/png");
                        //console.log('dataURL',dataURL);
                        this.loading=false;
                        this.transformPoster = dataURL;
                        // savePicture(dataURL)
                        //   if (this.imgUrl !== "") {
                        //     this.dialogTableVisible = true;
                        //   }
                    });
                },500)
                 
                 

        },
    },
}
</script>
<style lang='scss' scoped>
.poster-div{

}
.mut-poster-box{
    position:fixed;
    top:-10000px;
    left:-10000px;
    
    z-index:-5;
    padding:15px;
    width:85%;
    .muti-main-title{
        font-size:18px;
        font-weight:bold;
        text-align:left;
    }
    .share-info{
        margin:15px 0 20px 0;
        display:flex;
        justify-content:space-between;
        font-size:14px;
        color:#666;
        .avater-box{
            display:flex;
            align-items:center;
            font-size:16px;
            .name{
                margin-left:10px;
            }
        }
        .date{
            display:flex;
            align-items:center;
        }
    }
    .bottom-roki{
       border-top:1px solid #ddd;
       padding-top:15px;
       display:flex;
       justify-content: space-between;
        .sub-roki{
            display:flex;
            align-items: center;
            .name{
                margin-left:5px;
            }
        }
        .right-logo{
            display:flex;
            align-items: center;
        }
    }
}
.slide-in-elliptic-top-fwd{animation:slide-in-elliptic-top-fwd .7s cubic-bezier(.25,.46,.45,.94) both}
@keyframes slide-in-elliptic-top-fwd{0%{transform:translateY(-600px) rotateX(-30deg) scale(0);transform-origin:50% 100%;opacity:0}100%{transform:translateY(0) rotateX(0) scale(1);transform-origin:50% 1400px;opacity:1}}
</style>


