<template>
    <div class='muti-detail'>
        <div class='name'>{{mutiName}}</div>
        <div class='muti-down'>
            <div class='box-ul'>
                <div v-for='(item,index) in mutiArr' :key='index' class='muti-single'>
                    <div class='inner-box'>
                        <span class='num-label'>{{item.no}}</span>
                        <span class='tem-detail'>
                            {{item.modelName}} <span class='tem-text'>{{`${item.temperature?item.temperature:0}°C`}}</span>{{`${item.time}min`}}
                        </span>
                    </div>
                </div>
            </div>
        </div>
        <div class='pro-img'>
                <van-image
            width="110px" 
            fit="contain"
            :src="proInfo.dtIconUrl"
            />  
            <!--产品名称-型号-->
            <div class='pro-intro'>
                <span>{{proInfo.dtName?proInfo.dtName.split('-').join(' '):''}}</span>
                <!-- <span>DB610</span> -->
            </div>
        </div>
    </div>
</template>
<script>
 
import { getMutiDetail } from '@/api/lineViewsFromRokiApp';
export default {
    components:{
         
    },
    props:{
        mutiId:[String, Number],
    },
    data() {
        return {
           mutiName:'',
           mutiArr:[],
           proInfo:{
               dtName:''
           }
        }
    },
    created(){
       // const { avatar }=this.$route.query;
        //this.avatar=require('@/assets/images/db610.png');
        this.getMutiDetail();
    },
    methods:{
          //获取多段id
        getMutiDetail(){
            const id=this.mutiId;
            getMutiDetail(id).then(res=>{
                console.log('res',res);
                const {name,multiStepDtoList}=res.payload;
                multiStepDtoList.sort(function(a,b){return a.no-b.no})
                this.mutiName=name;
                this.mutiArr=multiStepDtoList; 
                this.proInfo=res.payload;
            })
        },
    },
}
</script>
<style lang='scss' scoped>
    .muti-detail{
        padding: 0 0px 15px;
        margin: 0 18px;
        opacity: 1;
        border-bottom: 1px solid rgba(165, 165, 165, 0.1);
        .muti-down{
            display:flex;
            justify-content: center;
            .box-ul{
                display:flex;
                flex-direction: column;
            }
        }
        .muti-single{
            text-align: left;
            margin-bottom:10px;
            .inner-box{
                // min-width:200px;
                .num-label{
                    width: 18px;
                    height: 18px;
                    border-radius: 50%;
                    background-color: #f0b62f;
                    display: inline-block;
                    text-align: center;
                    line-height: 17px;
                    font-size: 14px;
                    font-family: Open Sans-Regular, Open Sans;
                    font-weight: 400;
                    color: #000000;
                }
                .tem-detail{
                    display: inline-block;
                    font-size: 16px;
                    font-family: Roboto-Regular, Roboto;
                    font-weight: 400;
                    color: rgba(0, 0, 0, 0.8);
                    text-align: left;
                    margin-left: 2px;
                    .tem-text{
                        display: inline-block;
                        width: 58px;
                        margin-left: 5px;
                    }
                }
            }
        }
        .name{
            margin-bottom:25px;
            font-weight:bold;
            margin-bottom: 10px;
            font-size: 19px;
            font-family: OPPOSans-Bold, OPPOSans;   
            color: #333333;
        }
        .pro-img{
            margin-top: 12px;
            .pro-intro{
                margin:10px auto 7px;
                text-align:center;
                font-size: 14px;
                font-family: OPPOSans-Regular, OPPOSans;
                font-weight: 400;
                color: #333333;
            }
        }
    }
</style>



